<template>
  <div id="add-blog">
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
         <label>Vue.js</label>
         <input type="checkbox" value="vue.js" v-model="blog.categories">
         <label>Node.js</label>
         <input type="checkbox" value="Node.js" v-model="blog.categories">
         <label>React.js</label>
         <input type="checkbox" value="React.js" v-model="blog.categories">
         <label>Anglar4.js</label>
         <input type="checkbox" value="Anglar4.js" v-model="blog.categories">
      </div>
      <label>作者：</label>
      <input type="text" v-model="blog.author" required />
      <button v-on:click.prevent="post">发布</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客编辑成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="category in blog.categories">
          {{category}}
        </li>
      </ul>
      <p>作者：</p>
      <p>{{blog.author}}</p>

    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      id:this.$route.params.id,
      blog:{},
      authors:"",
      submmited:false

    }
  },
  methods:{
    fetchData(){
      this.$http.get('https://vuedemo-001.firebaseio.com/posts/' + this.id + ".json")
          .then(response => {
            // console.log(response.body);
            this.blog = response.body;
          })
    },
     post:function(){
        this.$http.put('https://vuedemo-001.firebaseio.com/posts/' + this.id + ".json"
        ,this.blog)
           .then(function(data){
             this.submmited = true;
           })
     }
  },
  created(){
    this.fetchData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing:border-box;
}

#add-blog{
  margin:20px auto;
  max-width:600px;
  padding:20px;
}

label{
  display:block;
  margin:20px 0 10px;
}

input[type="text"],textarea,select{
  display:block;
  width:100%;
  padding:8px;
}

textarea{
  height:158px;
}

#checkboxes label{
  display:inline-block;
  margin-top:0px;
}

#checkboxes input{
  display:inline-block;
  margin-right:10px;
}

button{
  display:block;
  margin:20px 0;
  background:crimson;
  color:#fff;
  border:0;
  padding:14px;
  border-radius:4px;
  font-size:18px;
  cursor:pointer;
}

#preview{
  padding:10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
}

h3{
  margin-top:10px;
}

</style>



